@media screen and (min-width:834px){  
    .skills {
        margin:0px 100px 50px 100px;
        /* white-space: nowrap; */
        display: flex;
       
    }
}

@media screen and (max-width:833.9px){
    .skills {
        margin:0px 0px 50px 0px;
        display: flex;
        justify-content: center ;
    }
}

.skills{
    color: #fff;
    width: 100px;
    display: flex;
    position: relative;
}

.skills::after{
    display: inline-block;
    content: '';
    height: 2px;
    bottom: 0;
    width: 0;
    position: absolute;
    background-color: rgb(79, 214, 79);
    transition: all 400ms ease-in-out ;
}
.skills:hover::after{
    transition: all 400ms ease-in-out ;
    width: 100%;
}

.skills-icons{
    width: 100%;
    display: flex;
    overflow:hidden;
}

.skills-icons img{
    width: 100px;
    height: 100px;
}
.skills-icons-individual-icon{
    width: 100px ;
    height: 100px ;
    position: relative;
    margin-left: 30px;
    animation: icons 10s infinite linear;
}

@keyframes icons{
    0%{left: 75%}
    100%{left: 0%}
}


