@media screen and (min-width:834px){  
    .project {
        margin:0px 100px 50px 100px;
        display: flex;
        flex-direction: column;
        /* background: rgb(10, 55, 153) !important; */
    }
}
@media screen and (max-width:833.9px){
    .project {
        margin:0px 0px 50px 0px;
        display: flex;
        justify-content: center ;
        flex-direction: column;
    }
}

.project-header{
    color: #fff;
    width: 120px;
    margin:0px 100px 50px 100px;
    display: flex;
    position: relative;
    
}

.project-header::after{
    display: inline-block;
    content: '';
    height: 2px;
    bottom: 0;
    width: 0;
    position: absolute;
    background-color: rgb(79, 214, 79);
    transition: all 400ms ease-in-out ;
}
.project-header:hover::after{
    transition: all 400ms ease-in-out ;
    width: 100%;
}

.img-content{
    grid-area: 1 / 1/ 1 /8;
    /* border: 1px solid whitesmoke; */
}

.project-content{
    grid-area: 1/7/1/-1;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    padding: 15px 0px;
}
.project-content2{
    grid-area: 1 / 1/ 1 /8;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    padding: 15px 0px;

}
.img-content2{
    grid-area: 1/7/1/-1;
}
.img{
    width: 100% !important;
    height: 100% !important;
    background-size:cover !important;
    background-color:rgba(0,0,0,0.7) !important;
    background-blend-mode:multiply !important;
   transition:all .5s ease-in-out !important;
}
.img:hover{
    cursor: pointer;
    background-color: transparent !important;
    transition:all .5s ease-in-out;
    /* border: 3px solid black; */
}
.project-title{
    text-align: end;
    font-family: cursive;
    color: #0aaaa5;
}

.project-title2{
    text-align:start;
    font-family: cursive;
    color: #0aaaa5;
}
.project-details{
    font-family: 'Cutive Mono', monospace;
    padding: 8px 10px;
    color: #fff;
    background-color: rgba(255, 255, 255, .15);  
    backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, .15);
    border-radius: 3px;
    line-height: 1.5;
}

.tech-stack ul{
    display: flex;
    list-style: none;
    color: #fff;
    justify-content: flex-end;
}
.tech-stack ul li{
    margin-left: 10px;
    font-family: 'Cutive Mono', monospace;

}

.project-links{
    display: flex;
    justify-content: flex-end;
}
.project-links *{
    cursor: pointer;
    margin-left: 20px;
}

.project-links2{
    display: flex;
    justify-content:flex-start
}

.project-links2 *{
    cursor: pointer;
    margin-right: 20px;
}

.tech-stack2 ul{
    display: flex;
    list-style: none;
    color: #fff;
    justify-content: flex-start;
}
.tech-stack2 ul li{
    margin-right: 10px;
    font-family: 'Cutive Mono', monospace;

}
@media (max-width:1207px){
    .img-content{
        grid-area: 1 / 1/ 1 /12;
        /* border: 3px solid whitesmoke; */
    }
    .project-content,  .project-content2{
        background-color: rgba(255, 255, 255, .09);  
        backdrop-filter: blur(5px);
        border: 1px solid rgba(255, 255, 255, .15);
        grid-area: 1/1/1/13;
    }
    .project-details{
        z-index: 3;
        background-color:transparent;  
        backdrop-filter:unset;
        border: 0px solid rgba(255, 255, 255, .15);
        border-radius: 0px;
    }
    .tech-stack2 ul{
        display: flex;
        justify-content: flex-end;
    }
    .tech-stack2 ul li{
        margin-left: 10px;
    }
    .project-links2{
        display: flex;
        justify-content:flex-end;
    }
    
    .project-links2 *{
        margin-left: 20px;
    }
    .project-title{
        text-align: start;
    }
}

