.contact-wrapper{
    color: #fff !important;
    width: 50%;
    background-color: rgba(255, 255, 255, 0.15);
    backdrop-filter: blur(5px);
    border-radius: 5px;
    border: 1px solid rgba(255, 255, 255, 0.15);
    display: flex;
    justify-content: center;
    flex-direction: column;

    }

.contact-intro-name{
    color:#444e5c;
    font-size: clamp(15px, 42px, 60px);
    font-family: 'Lato', sans-serif;
    font-weight:900;
    text-align: center;
}

.email-link{
    display: flex;
    justify-content: center;
}
.email-link a{
    display: grid;
    margin-top: 20px;
    background-color:  rgba(0, 255, 127,0.15);
    padding: 10px 15px;
    width: 150px;
    border: 1px solid  springgreen;
    border-radius: 5px;
    place-items: center;
}

.email-link a:hover{
    display: grid;
    margin-top: 20px;
    background-color:  rgba(0, 255, 127,0.3);
    padding: 10px 15px;
    width: 150px;
    border-radius: 5px;
    place-items: center;
}

.email-link-icon{
    display: flex;    
    color: #fff !important;
}

@media only screen and (max-width: 834px) {
    .contact-wrapper{
        width: 100%;
        }
}



.email-link-icon:hover{
    /* animation: email 4s linear; */
}
    @keyframes email{
        from {
            /* -moz-transform: rotateX(0deg); */
            /* -ms-transform: rotateY(0deg); */
            transform: rotateZ(0deg);
        }
        to {
            /* -moz-transform: rotateX(360deg); */
            /* -ms-transform: rotate(360deg); */
            transform: rotateZ(360deg);
        }
    }