
.sidebar-wrapper{
    
    background-color: rgba(0, 0, 0, 0.822);
    height: 100%;
    width: 100%;
    top:0;
    left: 0;
    z-index: 100 !important;
    position: fixed;
    display: flex;
    justify-content: flex-end;
    font-family: 'Titillium Web', sans-serif !important;
    scroll-margin: 0px;
}
.sidebar-menu{
    background-color:rgb(87, 87, 87);
    padding:20px 20px ;
    z-index: 100 !important;
    border: 1px solid black;
    transition: all 1s ease-in;
}
.sidebar-close-button{
    cursor: pointer;
    display: flex;
    flex-direction: row-reverse;
    color: #fff;
}
.sidebar-menu-links{
    display: flex;
    flex-direction: column;
}
.sidebar-links{
    color: #181818;
    display: flex;
    text-decoration: none;
    padding: 1rem 2rem;
    background-color:rgb(87, 87, 87);
    height: 100%;   
    letter-spacing: 1.4px;
    text-transform: uppercase;
    cursor: pointer;
    font-weight: 500;
    white-space: nowrap;
    margin-bottom: 40px;
    transition: all 200ms ease-in-out ;
    place-content: center;
    font-weight: 700 !important;
    position: relative;
}
.sidebar-links::after{
    display: inline-block;
    content: '';
    height: 2px;
    width: 0;
    position: absolute;
    bottom: 0;
    background-color: rgb(79, 214, 79);
    transition: all 400ms ease-in-out ;
}
.sidebar-links:hover::after{
    width: 100%;
    transition: all 400ms ease-in-out ;
}
.sidebar-resume-button{
border-radius: 4px;
padding: 20px 60px;
color: rgb(79, 214, 79);
border: 1px solid rgb(66, 228, 66);
cursor: pointer;
margin-top: 30px;
transition: all 0.2s ease-in-out;
text-decoration: none;
text-align: center;
text-transform: uppercase;
}
.sidebar-resume-button:hover {
    transition: all 0.2s ease-in-out;
    color: #fff;
    background-color: rgb(79, 214, 79);
}

@media (min-width:840px){
    div.sidebar-wrapper {
        display:none;
    }
}