.navbar-wrapper {
    height: 60px;
    display: flex;
    justify-content: space-between;
    width: 100%;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    padding: 0px 9%;
    z-index: 10;
    box-sizing: border-box;
    box-shadow: 0px 0px 10px rgba(0,0,0,.5);
}

.navbar-links {
   
    color: #fff !important;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0 1rem;
    margin-left: 10px;
    height: 100%;
    letter-spacing: 1.4px;
    text-transform: uppercase;
    cursor: pointer;
    font-weight: 500;
    white-space: nowrap;
    transition: all 200ms ease-in-out ;
}
.navbar-bar-menu .navbar-links{
border-bottom: 5px solid transparent;
transition: all 400ms ease-in-out ;
}
.navbar-bar-menu .navbar-links:hover{
border-width: 100%;
border-bottom: 5px solid #38B2AC;
transition: all 400ms ease-in-out ;

}
.navbar-bar {
    color: #fff !important;
    display: none;
}

.navbar-bar-menu {
    display: flex;
    align-items: center;
}

.navbar-bar-resume-button {
    border-radius: 4px;
    padding: 10px 22px;
    color: #fff;
    background-color:  #38B2AC;
    border:1px solid #38B2AC; 
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
    margin-left: 24px;
    text-align: center;
    line-height: 1.6;
    text-transform: uppercase;
}

.navbar-bar-resume-button:hover {
    transition: all 0.2s ease-in-out;
    color: #38B2AC;
    background: none;
    
}

.navbar-logo {
    display: none;
}

@media screen and (max-width: 835px) {
    .navbar-wrapper {
        padding: 0px 3em;
    }
    .navbar-bar{
        display: block;
        transform: translate(-50%, 55%);
        font-size: 1.8rem;
        cursor: pointer;

    }

    .navbar-bar-menu{
        display: none;
    }
   
}

.sidebar-wrapper{
    
    background-color: rgba(0, 0, 0, 0.822);
    height: 100%;
    width: 100%;
    top:0;
    left: 0;
    z-index: 100 !important;
    position: fixed;
    display: flex;
    justify-content: flex-end;
    font-family: 'Titillium Web', sans-serif !important;
    scroll-margin: 0px;
}
.sidebar-menu{
    background-color:rgb(87, 87, 87);
    padding:20px 20px ;
    z-index: 100 !important;
    border: 1px solid black;
    transition: all 1s ease-in;
}
.sidebar-close-button{
    cursor: pointer;
    display: flex;
    flex-direction: row-reverse;
    color: #fff;
}
.sidebar-menu-links{
    display: flex;
    flex-direction: column;
}
.sidebar-links{
    color: #181818;
    display: flex;
    text-decoration: none;
    padding: 1rem 2rem;
    background-color:rgb(87, 87, 87);
    height: 100%;   
    letter-spacing: 1.4px;
    text-transform: uppercase;
    cursor: pointer;
    font-weight: 500;
    white-space: nowrap;
    margin-bottom: 40px;
    transition: all 200ms ease-in-out ;
    place-content: center;
    font-weight: 700 !important;
    position: relative;
}
.sidebar-links::after{
    display: inline-block;
    content: '';
    height: 2px;
    width: 0;
    position: absolute;
    bottom: 0;
    background-color: rgb(79, 214, 79);
    transition: all 400ms ease-in-out ;
}
.sidebar-links:hover::after{
    width: 100%;
    transition: all 400ms ease-in-out ;
}
.sidebar-resume-button{
border-radius: 4px;
padding: 20px 60px;
color: rgb(79, 214, 79);
border: 1px solid rgb(66, 228, 66);
cursor: pointer;
margin-top: 30px;
transition: all 0.2s ease-in-out;
text-decoration: none;
text-align: center;
text-transform: uppercase;
}
.sidebar-resume-button:hover {
    transition: all 0.2s ease-in-out;
    color: #fff;
    background-color: rgb(79, 214, 79);
}

