@media screen and (min-width:834px){  
    .wrapper-intro {
        margin:0px 100px;
        white-space: nowrap;
    }
}

@media screen and (max-width:833.9px){
    .wrapper-intro {
        margin:0px 0px;
        display: flex;
        justify-content: center ;
    }
}
.wrapper-intro-top{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.intro-greeting{
    color:#64748B;
    font-size: clamp(10px, 18px, 180px);
    line-height: 1.6;
    font-family: 'Montserrat', sans-serif;
    font-weight:bold;
}
.wrapper-margin{
    margin-top: 20px;
}
.intro-name{
    color:#444e5c;
    font-size: clamp(15px, 62px, 80px);
    font-family: 'Lato', sans-serif;
    font-weight:900;

}
.intro-desc{
    font-family: 'Cutive Mono', monospace;
    color: #fff;
    font-size: clamp(12px, 32px, 80px);
    line-height: 1.2;
    font-weight:bold;
}


.intro-mail button {
    border: 1px solid #9a9ea3;
    background-color: #fff;
    border-radius: 5px;
    outline: none;
    padding: 15px 30px;
    font-weight:bold;
    cursor: pointer;
    transition: all .2s ease-in-out;
    margin-top: 10px;
}
.intro-mail button a{
    color: #000000;
}

.intro-mail button:hover{
    border: 1px solid #9a9ea3;
    transition: all .2s ease-in-out;
    background-color: #444e5c;
    border-radius: 5px;
    color:#fff ;
    outline: none;
    padding: 15px 30px;
    font-weight:bold;
}

.intro-mail button a:hover{
    color: #fff;
}
