@media screen and (min-width:912px){  
    .wrapper-about {
        display: grid;
        grid-template-columns: 3fr 2fr;
        place-items: center;
        gap: 50px;
       
    }
    #about h1 {
        margin:0px 100px 50px 100px;
        display: flex;
       
    }
}

@media screen and (max-width:833.9px){
    .wrapper-about {
        margin-top: 30px;
         display: grid;
         grid-template-rows: 1fr 2fr;
         place-items: center;
         gap: 50px;
  

    }
    .about-img{
        display: flex;
        justify-content: center;
    }
    #about h1 {
        margin:0px 0px 50px 0px;
        display: flex;

        justify-content: center ;
    }
}

.about-data{
  color: #fff ;
    font-family: 'Cutive Mono', monospace;
}
.about-data p{
    line-height: 1.3;
    font-weight: bold;
    margin-bottom: 20px;
    font-size: 18px;
  color: #fff;
    font-family: 'Cutive Mono', monospace;
}

.about-img{
    display: flex;
    justify-content: center;    
}
.about-img-overlay{
    width: 360px;
    height: 360px;
    border-radius: 50% !important;
    border:3px solid whitesmoke !important;
    overflow:hidden;
    transition:transform .3s ease-in-out ;
}
.about-img-overlay img{
    width: 360px;
}
.about-img-overlay:hover{
    transition:transform .3s ease-in-out ;
    transform:translate(-1%,-1%);
}


#about h1{
    width: 100px;
    display: flex;
     color: #fff ;
    position: relative;
}

#about h1::after{
    display: inline-block;
    content: '';
    height: 2px;
    bottom: 0;
    width: 0;
    position: absolute;
    background-color: rgb(79, 214, 79);
    transition: all 400ms ease-in-out ;
}
#about h1:hover::after{
    transition: all 400ms ease-in-out ;
    width: 100%;
}


.trainig-info ul{
    list-style:square;
    list-style-position: inside;
}

