
.main-github {
    color:#fff;
    display: none;
}
.main-linkedin{
    display: none;
    color: #0a66c2;
}
.main-github:hover,.main-linkedin:hover {
    color:grey;
}

.globe{
    display: none;
}

@media screen and (min-width:832px){
.main{
    position:fixed;
    /* transform:translate(10%,0%); */
    left: 0;
    height:36px;
    overflow:hidden;
    cursor: pointer;
    top:380px;
    transition:all .4s ease-in-out;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
.main:hover {
    padding: 10rem 0;   
    top:0;
    height:100%;
    transition:all .4s ease-in-out
}   

.globe{
    display: flex !important;
    color: #fff;
    margin:5px 0px;
}
.main-github {
    display: flex;
}
.main-linkedin{
    display: flex;
}

}

@media screen and (max-width:832px){
    .main{
        position:static;
        transition:all .4s ease-in-out;
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin-bottom: 20px;
        
    }
    .globe{
        display: none;
    }
    .main-github {
        display: flex;
        margin-right: 10px;
    }
    .main-linkedin{
        display: flex;
        margin-left: 10px;
    }
    
    }
    